.heroTable {
    padding-bottom: 100px;

    &__block {
        display: flex;
        justify-content: center;
    }

    &__top {
        max-width: 800px;
        display: flex;
        flex-direction: column;
    }

    &__subtitle {
        font-weight: normal;
        font-size: 18px;
        line-height: 25px;
        color: #605DEC;
        margin-bottom: 48px;
    }

    &__title {
        display: block;
        margin: auto;
        text-align: left;
        font-weight: 800;
        font-size: 50px;
        line-height: 68px;
    }
}