.filter {
    padding-top: 0;
    &__button {
        border: 1px solid #605DEC;
        border-radius: 4px;
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #605DEC;
        background: #fff;
        margin-right: 16px;
        &:hover {
            color: #FAFAFA;
            background: #605DEC;
        }
    }
}

.select {
    margin-top: 24px;
    margin-bottom: 48px;
    display: flex;
    max-width: 704px;
    flex-wrap: wrap;
    &__item {
        background: #FFF;
        color: #27273F;
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        border: 1px solid #CBD4E6;
        border-radius: 4px;
        margin-right: 24px;
    }

    &__img {
        margin-left: 4px;
        width: 18px;
        height: 18px;
    }
}