.table {
    &-block {
        width: 100%;
        border-radius: 8px;
        border: 1px solid #E9E8FC;
        padding: 16px;
    }

    &__string {
        margin-top: 8px;
        width: 100%;
        &:last-child .table__item {
            border-bottom: none;
            padding-bottom: 0;
        }
    }   

    &__item {
        position: relative;
        margin-top: 8px;
        padding: 8px 19px 16px 19px;
        // border-right: 1px solid #E9E8FC;
        border-bottom: 1px solid #E9E8FC;
        &:first-child {
            padding-left: 0;
        }
        &:last-child {
            border-right: 0;
            padding-right: 0;
            &::before {
                display: none
            }
        }
        &::before {
            position: absolute;
            right: 0;
            top: 8px;
            content: "";
            height: 85%;
            background: #E9E8FC;
            width: 1px;
        }

    }

    &__top, &__title {
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #27273F;
    }

    &__subtitle {
        font-weight: normal;
        font-size: 14px;
        line-height: 19px;
        color: #7C8DB0;
    }

    &__wrapper {
        display: flex;
        justify-content: center;
    }

    &__provider {
        display: flex;
        justify-content: flex-start;
        max-width: 250px;
    }

    &__speed, &__channel, &__mobile, &__option, &__price {
        text-align: center;
    }

    &__left {
        margin-right: 20px;
        text-align: center;
    }

    &__img {
        margin-right: 24px;
    }

    &__elem {
        text-align: center;
        margin-right: 5px;
        &:last-child {
            margin-right: 0;
        }
    }

    &__right {
        text-align: left;
    }

    &__stock {
        font-weight: bold;
        font-size: 14px;
        line-height: 19px;
        color: #605DEC;
    }

}

@media screen and (max-width: 1130px) {
    .table {
        &-block {
            width: 100%;
            border-radius: 8px;
            border: 1px solid #E9E8FC;
            padding: 10px;
        }
    
        &__string {
            margin-top: 8px;
            width: 100%;
            &:last-child .table__item {
                border-bottom: none;
                padding-bottom: 0;
            }
        }   

        &__top, &__title {
            font-size: 13px;
        }
    
        &__item {
            position: relative;
            margin-top: 8px;
            padding: 8px 8px 16px 8px;
            border-bottom: 1px solid #E9E8FC;
        }
    }
}