.rating {
    width: 100%;
    border: 1px solid #E9E8FC;
    border-radius: 8px;

    &__item {
        padding: 16px 0;
        text-align: center;
        font-weight: normal;
        font-size: 16px;
        line-height: 22px;
        color: #27273F;
    }

    &__wrapper {
        display: flex;
        justify-content: center;
        &-item {
            margin-right: 32px;
        }

        &-green {
            color: #27AE60;
        }

        &-black {
            color: #6E7491;
        }

        &-red {
            color: #EB5757;
        }
    }
}
