.hero {
    padding-top: 94px;
    padding-bottom: 245px;
    @media screen and (max-width: 700px) {
        padding-top: 40px;
        padding-bottom: 60px;
    }
    &-block {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    &__title {
        max-width: 800px;
        font-weight: 800;
        font-size: 50px;
        line-height: 68px;
    }
    &__subtitle {
        margin-top: 48px;
        margin-bottom: 48px;
        max-width: 800px;
        font-weight: normal;
        font-size: 18px;
        line-height: 25px;
        color: #605DEC;
    }
}