.pagination {
    margin-top: 24px;
    margin-bottom: 40px;
    &-block{
        display: flex;
        justify-content: center;
    }

    &__item {
        border: 2px solid #605DEC;
        background: #fff;
        color: #605DEC;
        display: flex;
        align-items: center;
        margin-right: 10px;
        &:last-child {
            margin-right: 0;
        }
        img {
            width: 10.97px;
            height: 8.44px;
        }
    }

    &__btn {
        
        &-right {
            transform: rotate(180deg);
        }
    }
}