.search {
    display: flex;
    border-radius: 4px;
    max-width: 1200px;

    &__wrapper {
        padding-left: 8px;
        position: relative;
        border: 1px solid #A1B0CC;
        box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
        border-radius: 4px;
        display: flex;
        align-items: center;
    }

    &__street {
        width: 554px;
    }

    &__home {
        width: 252px;
    }

    &__type {
        width: 200px;
    }

    &__input{
        border: none;
        width: 100%;
    }

    &__btn {
        box-shadow: 0px 2px 4px rgba(7, 4, 146, 0.1), 0px 24px 60px rgba(6, 47, 125, 0.05), 0px 12px 24px rgba(27, 59, 119, 0.05);
    }

    &__img {
        width: 32px;
        height: 32px;    
    }

}


@media screen and (max-width: 1210px) {
    .search {
        display: flex;
        flex-direction: column;
        width: 80%;
 
        &__wrapper {
            margin-bottom: 5px;
        }
        &__street, &__home, &__type {
            width: 100%;
        }
 
        &__btn {
             margin-top: 15px;
             text-align: center;
        }
     }
    
    
}

@media screen and (max-width: 900px) {
    .search {
       display: flex;
       flex-direction: column;
       width: 80%;

       &__wrapper {
           margin-bottom: 5px;
       }

        &__btn {
            margin-top: 15px;
            text-align: center;
        }
    }
    
    
}
